/* imports */
import 'lazysizes';
var $ = require('jquery');
var Popper = require('popper.js');
var objectFitImages = require('object-fit-images');
import { TweenMax, TimelineLite, Power2, Elastic, CSSPlugin } from "gsap/TweenMax";
import barba from '@barba/core';
import barbaPrefetch from '@barba/prefetch';
import CustomEase from './vendor/CustomEase';
barba.use(barbaPrefetch);

if ('scrollRestoration' in history) {
  history.scrollRestoration = 'manual';
}

$('body').removeClass('nojs');

/* helpers */
let windowHeight = '';
const appHeight = () => {
  if( windowHeight !== window.innerHeight ) {
    windowHeight = window.innerHeight;
    const doc = document.documentElement;
    doc.style.setProperty("--app-height", `${windowHeight}px`);
  }
};

function checkAppHeight() {
  appHeight();
  requestAnimationFrame(checkAppHeight);
}
requestAnimationFrame(checkAppHeight);

/* transitions */
let transitionDuration = 300;
let defaultEasing = CustomEase.create("custom", "M0,0 C0.07,0.95 0,1 1,1");

barba.hooks.beforeEnter((data) => {
  $(window).scrollTop(0); // scroll to top here
  // $('img').each(function(idx, elem){
  //    lazySizes.loader.unveil(elem);
  //  });

  // update body class
  let regexp = /\<body.*\sclass=["'](.+?)["'].*\>/gi,
          match = regexp.exec(data.next.html);
      match[1] += ' in-transition';
      if(!match || !match[1]) return;
      document.body.setAttribute('class', match[1].replace('nojs',''));

  let faviconRegexp = /\<link.*? href="(.*?.ico)".*\>/gi,
      faviconMatch = faviconRegexp.exec(data.next.html);
      if(!faviconMatch || !faviconMatch[1]) return;
      $('#favicon').attr('href', faviconMatch[1]);

  // update header
  var headerContent = $(data.next.html).find('.update-content');
  $('.update-content').replaceWith(headerContent);
});

barba.init({
  debug: false,
  prevent: ({ el }) => el.classList && el.classList.contains('prevent'),
  transitions: [{
    "name": "home",
    to: {
      namespace: ["home"]
    },
    from: {
      namespace: ["brand"]
    },
    before: function(data) {
      const done = this.async();
      $('header').css({opacity: 0});
      $('body').removeClass('mobile-menu-open quick-links-open mobile-interior-section-nav--is-open');
      $('body').addClass('in-transition home');
      TweenLite.to($('.page-section--main'), 1, {ease: defaultEasing, opacity:0, onComplete: function(){
        done();
      } } );
    },
    beforeEnter: function(data) {
      const done = this.async();
      $('header').css({opacity: 0});
      done();
    },
    afterEnter: function(data) {
      const done = this.async();
      init();
      done();
    }
  }, {
    "name": "brand",
    to: {
      namespace: ["brand"]
    },
    from: {
      namespace: ["home","brand"]
    },
    before: function(data){
      $('body').removeClass('mobile-menu-open quick-links-open mobile-interior-section-nav--is-open');
      if( data.next.url.path == '/firefox/' || data.next.url.path == '/design/firefox/' ) {
        $('body').addClass('in-transition firefox-transition');
      }
      else if( data.next.url.path.includes("/mozilla/") ) {
        $('body').addClass('in-transition mozilla-transition');
      } else {
        $('body').addClass('in-transition white-transition');
      }
    },
    leave: function(data){
      const done = this.async();
      $(data.current.container).append('<div class="screen"></div>');
      $(data.next.container).append('<div class="screen active"></div>');
      let targetY = 0;
      let regexp = /\<body.*\sclass=["'](.+?)["'].*\>/gi,
              match = regexp.exec(data.next.html);
          if(match[1].indexOf('page-child') != -1 ) {
            targetY = $('header').outerHeight();
          }

      TweenLite.to($('.screen'), 1, {ease: defaultEasing, y:targetY, onComplete: done } )
    },
    enter: function(data) {
      const done = this.async();
      done();
    },
    afterEnter: function(data) {
      var done = this.async();
      init();
      done();
    },
    after: function(data) {
      const done = this.async();
      TweenLite.to($('.screen'), 0.6, {ease: Power2.easeInOut, opacity: 0, onComplete: function(){
        $('.screen').remove();
        $('body').removeClass('mozilla-transition firefox-transition white-transition');
        $('.section-nav').removeClass('stuck');
        $('body').removeClass('in-transition');
        done();
      }});
    }
  }]
});

/* other functionality */

$(document).ready(function(){
  init();
});

const init = function() {

  if( $('.homepage-loader').length ) {
    initHomepageLoader();
  }

  initNav();

  initSectionNav();
  initCodeTabs();
  initZaps();
  initTypetesters();
  initColorTables();
  if( $('body').hasClass('home') ) {
    $('body').addClass('in-transition');
    transitionHome();
  }
  objectFitImages();
  $('.section-nav').removeClass('stuck');

  if( $('.card-listing').length ) {
    $(window).on('resize', function(){
      checkBrandInteractives();
    }).trigger('resize');
    $(document).on('lazyloaded', function(e){
      checkBrandInteractives();
      lazySizes.autoSizer.checkElems();
    });
  }

  initBrandInteractives();
  lazySizes.autoSizer.checkElems();
}

const checkBrandInteractives = function() {
  $('.brand-interactive').each(function(){
    // let targIframe = $(this).find('iframe');
    // targIframe.css({
    //   width: ( $(window).width() > 1000 ) ? $(window).width() : 1000,
    //   height: ( $(window).width() > 1000 ) ? $(window).width() : 2000
    // });
    lazySizes.autoSizer.checkElems();
  });
}

const initHomepageLoader = function() {
  $('img').each(function(idx, elem){
     lazySizes.loader.unveil(elem);
   });
  $('body').addClass('homepage-loader-animation');
  setTimeout(function(){
    $('.homepage-loader').addClass('state-1');
  }, 10);

  setTimeout(function(){
    $('.homepage-loader').removeClass('state-1').addClass('state-2');
  }, 250);

  setTimeout(function(){
    $('.homepage-loader').removeClass('state-1').removeClass('state-2').addClass('state-3');
  }, 500);

  setTimeout(function(){
    $('body').removeClass('homepage-loader-animation');
    $('.homepage-loader').remove();
  }, 1000);
}

const transitionHome = function() {
  var tl = new TimelineLite();
  tl.add( TweenLite.to($('header'), 1, {ease: Power2.easeOut, opacity:1} ) );
  tl.staggerTo($('.card-listing--card'), 0.5, {y:0, opacity: 1}, 0.15, "+=0", function(){
    $('body').removeClass('in-transition');
  });
  tl.play();
  //
  // $('img').each(function(idx, elem){
  //    lazySizes.loader.unveil(elem);
  //  });
}

const initNav = function(){
  function checkHasScrolled() {
    if( $(window).scrollTop() == 0 ) {
      $('body').removeClass('has-scrolled');
    } else {
      $('body').addClass('has-scrolled');
    }
    if( $(window).scrollTop() >= $(window).height() ) {
      $('body').addClass('has-scrolled-100vh');
    } else {
      $('body').removeClass('has-scrolled-100vh')
    }
  }
  $(window).on('scroll', function(){
    requestAnimationFrame(checkHasScrolled)
  }).trigger('scroll');

  $('.mobile-nav-toggle').on('click', function(e){
    e.preventDefault();
    $('body').toggleClass('mobile-menu-open');
  });

  $('.mobile-nav .menu-item-has-children > a .arrow').remove();

  $('.mobile-nav .menu-item-has-children > a').addClass('prevent').append('<span class="arrow"></span>');
  $('.mobile-nav .menu-item-has-children > a > .arrow').on('click', function(e){
    e.preventDefault();
    e.stopPropagation();
    $(this).parents('.menu-item-has-children').toggleClass('open');
  });

  $('.quick-links-toggle').on('click', function(e){
    e.preventDefault();
    $('body').toggleClass('quick-links-open');
  });
}

const initSectionNav = function() {
  $(window).on('scroll', function(){
    if( $('.bumper, footer:visible').length ) {
      let screenBottomOffset = $(window).scrollTop() + $(window).height(),
          bumperOffset = $('.bumper, footer').offset().top;

      if( bumperOffset <= screenBottomOffset ) {
        $('.section-nav').addClass('stuck');
      } else {
        $('.section-nav').removeClass('stuck');
      }
    }

    $('body').removeClass('mobile-interior-section-nav--is-open');
  }).trigger('scroll');

  $('.mobile-interior-section-nav .section-toggle').on('touchstart click', function(e){
    e.preventDefault();
    $('body').toggleClass('mobile-interior-section-nav--is-open');
  });
}

const initCodeTabs = function() {
  if( $('.code-block').length ) {
    $('.code-block').each(function(idx, elem){
      $(elem).find('.code-tabs a').on('click', function(e){
        e.preventDefault();
        let target = $(this).attr('href');
        $(this).parents('.code-tabs').find('.active').removeClass('active');
        $(this).addClass('active');
        $(this).parents('.code-block').find('.code-tab-content').removeClass('active');
        $(this).parents('.code-block').find('[data-attr-tab-id="'+target+'"]').addClass('active');
      });
    });
  }
}

const initZaps = function() {
  $('.wysiwyg-content a[title]').each(function(idx, elem){
    // override title with zap
    $(elem).attr('data-zap-text', $(elem).attr('title'));
    $(elem).addClass('has-zap');
    $(elem).parent().addClass('zap-container');
  });

  $('.has-zap').each(function(idx, elem){
    $(elem).on('click', function(e){
      e.preventDefault();
    });
    $(elem).attr('data-zap-target', idx);
    let text = $(elem).attr('data-zap-text'),
        styles = ['style-1', 'style-2', 'style-3'],
        placements = ['top-end', 'bottom-end', 'right'],
        randStyle = styles[Math.floor(Math.random()*styles.length)],
        randPlacement = placements[Math.floor(Math.random()*placements.length)];

    $(elem).parent().append('<div class="zap" data-zap-id="'+idx+'"><span x-arrow class="arrow '+randStyle+'"></span><div class="zap-text">'+text+'</div></div>');
    let zap = $('[data-zap-id="'+idx+'"]'),
        ref = $(elem),
        popperInstance = new Popper(ref, zap, {
          placement: randPlacement,
          flip: {
              behavior: placements
          },
          modifiers: {
            offset: '-50%'
          }
        });
  });

  $('.has-zap').on('mouseover', function(){
    $('[data-zap-id="'+$(this).attr('data-zap-target')+'"]').addClass('visible');
  }).on('mouseout', function(){
    $('[data-zap-id="'+$(this).attr('data-zap-target')+'"]').removeClass('visible');
  })
}

const initTypetesters = function() {
  $('.type-controls').each(function(){
    var dropdown = $(this).find('select'),
        rangeWrap = $(this).find('.range-select'),
        rangeInput = rangeWrap.find('input'),
        parentDiv = $(this).parents('.wysiwyg-content');

    dropdown.on('change', function(){
      parentDiv.find('.type-output').removeClass(function (index, css) {
         return (css.match (/(^|\s)zillaslab\S+/g) || []).join(' ');
      });
      parentDiv.find('.type-output').addClass($(this).val());
    });

    rangeInput.on('input change', function(){
      parentDiv.find('.type-output').css({
        fontSize: $(this).val()+'px'
      });
      rangeWrap.find('.value').text($(this).val()+'px');
    })
  });
}

const initColorTables = function() {
  $('.color-table').find('[data-hex]').on('mouseover', function(){
    let label = '';
    if( $(this).attr('data-label') != "" ) {
      label +=  $(this).attr('data-label')+'<br>';
    }
    if( $(this).attr('data-hex') != "" ) {
      label +=  $(this).attr('data-hex');
    }
    $(this).parents('.row').find('.current').html(label).show();
  });

  $('.color-table').on('mouseout', function(){
    $(this).parents('.row').find('.current').hide();
  });

  $('.color-table').find('[data-hex]').each(function(idx, elem){
    let text = 'Click to copy Hex value';
    $(elem).parent().append('<div class="zap" data-zap-id="color-table-'+idx+'"><span class="arrow"></span><div class="zap-text">'+text+'</div></div>');
    let zap = $('[data-zap-id="color-table-'+idx+'"]'),
    ref = $(elem),
    popperInstance = new Popper(ref, zap, {
      positionFixed: true,
    });
    $(elem).on('mouseover', function(){
      $('[data-zap-id="color-table-'+idx+'"]').addClass('visible');
    }).on('mouseout', function(){
      $('[data-zap-id="color-table-'+idx+'"]').removeClass('visible');
    })
  });

  $('.color-table').find('[data-hex]').on('touchstart', function(e){
    e.stopPropagation();
    e.preventDefault();
  });
  $('.color-table').find('[data-hex]').off('click').on('click', function(e){
    e.preventDefault();
    let str = $(this).attr('data-hex');
    const el = document.createElement('textarea');
    el.value = str;
    document.body.appendChild(el);
    el.select();
    document.execCommand('copy');
    document.body.removeChild(el);
    $(this).parents('.row').find('.current').append('<br>Copied!');
  });

  $(window).on('resize', function(){
    $('.tall .color').each(function(idx, elem){
      $(elem).css({
        height: $(elem).parents('.tall').height()
      });
    });
  }).trigger('resize');
}

const initBrandInteractives = function() {
  if( $('#blobs-kLEsZ:not(".active")').length ) {
    var svgElSelector = '#blobs-kLEsZ';

    var svgEl = document.querySelector(svgElSelector);
    var mousePosition = [0, 0];
    var scrollY = 0;

    var classes = [
      'top left', 'top center', 'top right',
      'mid left', 'mid center', 'mid right',
      'bottom left', 'bottom center', 'bottom right'
    ];

    var points;
    initPoints();

    if (window.PointerEvent) {
      svgEl.addEventListener('pointermove', throttle(onPointerMove, 100));
    } else {
      svgEl.addEventListener('mousemove', throttle(onPointerMove, 100));
      svgEl.addEventListener('touchmove', throttle(onPointerMove, 100));
    }

    window.addEventListener('resize', debounce(onResize, 300));

    function onPointerMove(event) {
      mousePosition = [event.clientX, event.clientY];

      var distances = [];

      for (var i = 0; i < points.length; i++) {
        var point = points[i];
        var distance = Math.hypot(point[0] - mousePosition[0], point[1] - mousePosition[1]);
        distances.push(distance);
      }

      var closestPointIndex = distances.indexOf(Math.min.apply(Math, distances));

      svgEl.setAttribute('data-location', classes[closestPointIndex]);
    }

    function onResize(event) {
      initPoints();
    }

    function initPoints() {
      points = [
        [0, 0],                      [window.innerWidth / 2, 0],                      [window.innerWidth, 0],
        [0, window.innerHeight / 2], [window.innerWidth / 2, window.innerHeight / 2], [window.innerWidth, window.innerHeight / 2],
        [0, window.innerHeight],     [window.innerWidth / 2, window.innerHeight],     [window.innerWidth, window.innerHeight]
      ];
    }

    function throttle(callback, limit) {
      var wait = false;
      return function() {
        if (!wait) {
          var context = this;
          var args = arguments;
          callback.apply(context, args);
          wait = true;
          setTimeout(function() {
            wait = false;
          }, limit);
        }
      }
    }

    function debounce(callback, wait) {
      var timeout;
      return function() {
        var context = this;
        var args = arguments;
        var later = function() {
          timeout = null;
        }
        var callNow = !timeout;
        clearTimeout(timeout);
        timeout = setTimeout(later, wait);
        if (callNow) callback.apply(context, args);
      }
    }

    $('#blobs-kLEsZ').addClass('active');
  }

}
